import { HStack, VStack, Icon, Text, ScrollView } from "native-base";
import { Feather } from '@expo/vector-icons'
import { Area } from "./Area";

interface ActuationsProps {
   actuations: {
      id: number;
      titulo: string;
      descricao: string;
   }[]
}

export function Actuations({ actuations}: ActuationsProps) {
   return (
      <VStack borderBottomWidth={1} borderBottomColor="gray.500" pb={6} mt={2}>
         <HStack mb={2} space={2}>
            <Icon as={Feather} name="list" size={[4, 5]} color="green.700"/>
            <Text color="green.700" fontSize={["xs", "md"]} fontFamily="heading">Áreas de atuação</Text>
         </HStack>
         <ScrollView h={[24, 36]} indicatorStyle="white" persistentScrollbar={true}>
            { actuations.map(item => {
               return <Area name={item.titulo} description={item.descricao} key={item.titulo}/>
            })}
         </ScrollView>
      </VStack>
   )
}
