import { Center, Text, Actionsheet, Button, VStack, Box, Icon, useDisclose } from 'native-base'
import { ImageBackground } from 'react-native'
import { MaterialIcons } from '@expo/vector-icons'
import { useNavigation, useIsFocused, useFocusEffect } from '@react-navigation/native'
import { styles } from '../styles/backgroundImage';

export function Welcome() {
   const { navigate } = useNavigation();
   const { onClose, isOpen, onOpen} = useDisclose();
   
   function handleNavigateForProfile() {
      navigate('profile', { id: '8a00a3d8-5954-4be8-b76f-7e668518eb61' })
   }

   useFocusEffect(() => {
      onOpen();
   })

   return (
      <Center flex={1} bg="gray.900" position="static">
         <ImageBackground 
           source={require('../../assets/bg.jpg')}
           style={styles.backgroundImage}
         >
            <Actionsheet isOpen={isOpen} size='full' hideDragIndicator>
               <Actionsheet.Content bg="gray.900" height={["xs", "sm"]}>
                  <VStack w="full" h="full" pt={16} py={8} px={4} justifyContent='space-between'>
                     <Box>
                        <Text 
                           color="green.600" 
                           fontFamily="heading" 
                           fontSize={["md", "lg"]}
                           textAlign="center"
                           mb={2}
                        >
                           Em busca de um Profissional? Está { '\n' } no lugar certo!
                        </Text>
                        <Text 
                           color="gray.500" 
                           textAlign="center"
                           fontSize={["xs", "sm"]}
                           fontFamily="body"
                           width="full"
                        >
                           Precisando de um serviço? Não perca tempo, acesse e encontre profissionais 
                           de diferentes áreas e lugares.
                        </Text>
                     </Box>
                     <Button 
                        bg="green.600"
                        rounded="full"
                        height={[12, 14]}
                        _pressed={{
                           bg: "green.700"
                        }}
                        onPress={() => {handleNavigateForProfile(), onClose()} }
                     > 
                        <Text fontFamily="heading" color="white">Acessar agora</Text>        
                     </Button>
                  </VStack>
               </Actionsheet.Content>
                  <Box 
                     position="absolute" 
                     w={16} h={16} 
                     bg="green.600" 
                     alignItems="center" 
                     justifyContent="center"
                     rounded="full"
                     bottom={[290, 350]}
                  >
                     <Icon as={MaterialIcons} name="engineering" color="white" size={[7, 8]}/>
                  </Box>
               </Actionsheet>
         </ImageBackground>
      </Center>
   )
}

