import { Center, Image, Text, VStack } from "native-base";
import { Alert } from "../components/Alert";

export function ProfileNotFound() {
   return (
      <VStack flex={1} bg="gray.900" p={4}>
         <Center flex={1}>
            <Image source={require('../../assets/professional-not-found.png')} alt="Profissional não encontrado" size="xl"/>
            <Text color="white" textAlign="center" px={4} mt={6} fontSize={20} fontFamily="heading">
               <Text color="green.600">Ops! </Text>
               Não conseguimos encontrar nenhum profissional.
            </Text>
            <Text color="gray.500" textAlign="center">
               Não encontramos nenhum registro que corresponde a sua busca, verifique e tente novamente.
            </Text>
         </Center>
         
      </VStack>
   )
}