import { Box, HStack, Icon, Text, VStack } from "native-base";
import { MaterialIcons } from '@expo/vector-icons'

interface AtendancesProps {
   attendancePlaces: string;
}

export function Atendances({ attendancePlaces }: AtendancesProps) {
   return (
      <VStack mb={12}>
         <HStack mt={4} mb={1} space={1} alignItems="center">
            <Icon as={MaterialIcons} name="location-on" size={[4, 5]} color="green.700"/>
            <Text color="green.700" fontSize={["xs", "md"]} fontFamily="heading">Atendimentos</Text>
         </HStack>
         <Text color="gray.400" fontSize={["xs", "sm"]}>{ attendancePlaces }</Text>
      </VStack>
   )
}