import { Box, Text } from "native-base"

interface AreaProps {
   name: string;
   description: string;
}

export function Area({ name, description }: AreaProps) {
   return (
      <Box mb={4} mr={4}>
         <Text color="white" fontSize={["xs", "sm"]} fontFamily="heading">{name}</Text>
         <Text color="gray.400" fontFamily="body" fontSize={["xs", "sm"]}>{description}</Text>
      </Box>
   )
}