import { Entypo, Feather, FontAwesome } from '@expo/vector-icons';
import { useNavigation, useRoute } from '@react-navigation/native';
import { Box, HStack, Icon, Image, Text, VStack } from 'native-base';
import { useEffect, useState } from 'react';
import { Share, TouchableOpacity } from 'react-native';

import { ApiResponseProfile, ProfessionalProfile } from '../../@types/Professional';
import { Loading } from '../../components/Loading';
import { api } from '../../services/api';
import { Actuations } from './Actuations';
import { Atendances } from './Atendances';
import { Contacts } from './Contacts';

type ParamsProps = {
   id: string;
}

export function Profile() {
   const route = useRoute();
   const { id } = route.params as ParamsProps;

   const { navigate } = useNavigation();
   const [professional, setProfessional] = useState<ProfessionalProfile | null>();

   async function getDataProfile() {
      const response = await api.get<ApiResponseProfile>(id)
      if(response.data.meta.results)
         setProfessional(response.data.meta.results);
      else 
         return navigate('error');
   }

   async function shareProfileLink() {
      await Share.share({
         message: `https://serverino.netlify.app/${id}`,
      });
   }

   useEffect(() => {
     getDataProfile().then(() => true);
   }, [id]) 

   if (!professional) return <Loading />

   return (
      <VStack flex={1} bg="gray.900">
         <Image source={require('../../../assets/profile.jpg')} alt="Imagem de um notbook" h={["40", "1/4"]} />
         <VStack 
            mt={-5}
            px={5} 
            justifyContent="space-between" 
            roundedTopLeft={15}
            roundedTopRight={15}
            bg="gray.900"
         >
            <VStack mt={-10}>
               <HStack justifyContent="space-between" alignItems="center">
                  <Image 
                     source={require('../../../assets/photo-profile.jpg')} 
                     alt='Imagem de perfil'
                     size={[20, 32]} 
                     rounded="full"
                     borderWidth={2}
                     borderColor="white"
                  />

                  <HStack space={2} alignItems="center" mt={16}>
                     <Icon as={FontAwesome} name="star" size={[4, 5]} color="white"/>
                     <Text color="white" mr={2} fontSize={["xs", "sm"]} >Novo</Text>
                     <TouchableOpacity onPress={shareProfileLink}>
                        <Icon as={Entypo} name="share" size={[5, 6]} color="white"/>
                     </TouchableOpacity>
                  </HStack>
               </HStack>
               
               <Box mt={5}>
                  <HStack space={[1, 2]} alignItems="center">
                     <Icon as={Feather} name="code" size={[4, 5]} color="green.600"/>
                     <Text 
                        color="green.300" 
                        fontSize={{ base: "xs", sm: "sm" }} 
                        fontFamily="heading">{ professional?.area.nome}
                     </Text> 
                  </HStack>
                  <Text color="white" fontFamily="heading" fontSize={{ base: "sm", sm: "lg" }} mb={2}>
                     { professional?.nome_completo }
                  </Text>
               </Box>
               
               <Actuations actuations={ professional.atuacoes }/>
               <Atendances attendancePlaces={ professional.locais_atendimento }/>
            </VStack>
         </VStack>
         
         <Contacts phone={professional.telefone} whatsapp={professional.zap}/>
      </VStack>
   )
}
