import { Box, HStack, Icon, Text } from 'native-base';
import { Linking, TouchableOpacity } from 'react-native';
import { FontAwesome } from '@expo/vector-icons';

interface AreaContactProps {
   phone: string;
   whatsapp: string;
}

export function Contacts({ phone, whatsapp }: AreaContactProps) {
   return (
      <Box 
         bg="gray.800" h="20" 
         justifyContent="center" 
         px={10} position="absolute" 
         bottom={0} 
         right={0}
         left={0}
         roundedTop={20}
      >
         <HStack justifyContent="space-around" w="full">
            <TouchableOpacity 
               onPress={ async () => await Linking.openURL(`whatsapp://send?text=Olá&phone=+55${whatsapp}`)}
            >
               <HStack space={2} alignItems="center">
                  <Icon as={FontAwesome} name="whatsapp" size={[5, 6]} color="green.600"/>
                  <Text color="green.600" fontSize={["xs", "sm"]}>WhatsApp</Text>
               </HStack>
            </TouchableOpacity>

            <Box w={0.5} h="full" bg="gray.700"></Box>

            <TouchableOpacity 
               onPress={ async () => await Linking.openURL(`tel:${phone}`) }
            >
               <HStack space={2} alignItems="center">
                  <Icon as={FontAwesome} name="phone" size={[5, 6]} color="white"/>
                  <Text color="white" fontSize={["xs", "sm"]}>Telefone</Text>
               </HStack>
            </TouchableOpacity>
         </HStack>
         </Box>
   )
}