import { NavigationContainer } from '@react-navigation/native'
import * as Linking from 'expo-linking';
import { StackRoutes } from './stack.routes'

const prefix = Linking.createURL('/');

const linking = {
   prefixes: [prefix, 'exp://192.168.1.105:19000/--/', 'https://serverino.netlify.app/', 'http://app.serverino.com.br/'],
   config: {
      screens: {
         welcome: {
            path: 'welcome',
         },
         profile: {
            path: 'profile/:id',
            parse: {
               id: (id: string) => `${id}`,
            },
         },
         notFound: '*'
      },
   }
};

export function Routes() {
   return (
      <NavigationContainer linking={linking} >
         <StackRoutes />
      </NavigationContainer>
   )
}